import { Button, Col, Image } from 'react-bootstrap'
import { Link } from 'react-router-dom';
import useLenguage from '../hooks/useLenguage';
import { useResize } from '../hooks/useResize';
import useTheme from '../hooks/useTheme';

export const Icons = ({ url, title, MouseEnter }) => {
    const { theme } = useTheme();
    return (
        <Col lg={3} className='col-4 text-center'>
            <Button
                variant={
                    theme
                        ? 'outline-dark'
                        : 'outline-light'
                }

                onMouseMove={(e) => MouseEnter(e, { url, title })}
                onClick={(e) => MouseEnter(e, { url, title })}
                className={`${theme ? "About_left-icons" : "About_left-icons-dark"} px-0`}
            >
                <Image src={url} alt={title} />
                <div>{title}</div>
            </Button>
        </Col>
    )
}
export const IconsPortfolio = ({ url, img, title }) => {
    const { theme } = useTheme();
    const { lenguage } = useLenguage();
    const resize = useResize();
    const [Width] = resize !== 0 ? resize : [0, 0];
    return (
        <Col sm={6} className={"col-12 p-0"} style={{ overflow: "hidden" }}>
            <div
                className='Porfolio_incon'
                style={{
                    backgroundImage: `url(${img})`,
                    height: `${Width > 765
                            ? "20vw"
                            : Width < 576
                                ? "40vw"
                                : "25vw"
                        }`
                }}
            >
                <Link
                    exact="true"
                    to={url}
                    className={`${theme
                        ? 'Porfolio_incon-url'
                        : 'Porfolio_incon-url-dark'
                        }`}
                >
                    <div className={`${theme ? 'Porfolio_incon-title' : 'Porfolio_incon-title-dark'}`}>{lenguage ? "Go" : "Ir"}</div>
                </Link>
            </div>
        </Col>
    )

}