import React, { useCallback, useRef, useState } from 'react'
import { Button, Col, Image, Row, Form } from 'react-bootstrap'
import { FormGroup } from '../components/FormGroup';
import useLenguage from '../hooks/useLenguage';
import useTheme from '../hooks/useTheme';
import githubW from '../asset/github-white.svg';
import githubB from '../asset/github-black.svg';
import linkedinW from '../asset/linkedin-square-White.svg';
import linkedinB from '../asset/linkedin-square-Black.svg';
import whatsappW from '../asset/whatsapp-white.svg';
import whatsappB from '../asset/whatsapp-black.svg';
import { useResize } from '../hooks/useResize';
import PostMail from '../hooks/useGet';
import { Error } from '../components/error';
export const Contact = () => {
    const [form, setForm] = useState({});
    const [post, setPost] = useState({});
    const [error, setError] = useState(null)
    const { theme } = useTheme();
    const { lenguage, LenguageText } = useLenguage();
    const { es, en } = LenguageText;
    const resize = useResize();
    const [Width] = resize !== 0 ? resize : [0, 0];
    const SpanName = useRef();
    const SpanEmail = useRef();
    const SpanSubject = useRef();
    const SpanMessage = useRef();
    const GitHub_ICON = useRef();
    const Linkedin_ICON = useRef();
    const WhatsApp_ICON = useRef();
    // eslint-disable-next-line react-hooks/exhaustive-deps
    const regexEmail = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,4})+$/;


    const handleForm = useCallback((e) => {
        switch (e.target.name) {
            case "name": {
                if (!form.name) {
                    setPost({
                        ...post,
                        [e.target.name]: false
                    })
                    return setForm({
                        ...form,
                        [e.target.name]: e.target.value,
                    })
                } else if (form.name.length >= 29) {
                    SpanName.current.classList.remove('good');
                    SpanName.current.classList.add('error');
                    setPost({
                        ...post,
                        [e.target.name]: false
                    })
                    return setForm({
                        ...form,
                        [e.target.name]: form.name.slice(0, -1),
                    })
                } else if (form.name.length < 7) {
                    SpanName.current.classList.remove('good');
                    SpanName.current.classList.add('error');
                    setPost({
                        ...post,
                        [e.target.name]: false
                    })
                    return setForm({
                        ...form,
                        [e.target.name]: e.target.value,
                    })
                } else {
                    SpanName.current.classList.remove('error');
                    SpanName.current.classList.add('good');
                    setPost({
                        ...post,
                        [e.target.name]: true
                    })
                    return setForm({
                        ...form,
                        [e.target.name]: e.target.value,
                    })
                }
            }
            case "email": {
                if (!form.email && !regexEmail.test(form.email)) {
                    setPost({
                        ...post,
                        [e.target.name]: true
                    })
                    return setForm({
                        ...form,
                        [e.target.name]: e.target.value,
                    })
                } else if (form.email.length >= 29) {
                    SpanEmail.current.classList.remove('good');
                    SpanEmail.current.classList.add('error');
                    setPost({
                        ...post,
                        [e.target.name]: false
                    })
                    return setForm({
                        ...form,
                        [e.target.name]: form.email.slice(0, -1),
                    })
                } else if (form.email.length < 8) {
                    SpanEmail.current.classList.remove('good');
                    SpanEmail.current.classList.add('error');
                    setPost({
                        ...post,
                        [e.target.name]: false
                    })
                    return setForm({
                        ...form,
                        [e.target.name]: e.target.value
                    })
                } else if (!regexEmail.test(form.email)) {
                    SpanEmail.current.classList.remove('good');
                    SpanEmail.current.classList.add('error');
                    setPost({
                        ...post,
                        [e.target.name]: false
                    })
                    return setForm({
                        ...form,
                        [e.target.name]: e.target.value
                    })
                } else {
                    SpanEmail.current.classList.remove('error');
                    SpanEmail.current.classList.add('good');
                    setPost({
                        ...post,
                        [e.target.name]: true
                    })
                    return setForm({
                        ...form,
                        [e.target.name]: e.target.value
                    })
                }
            }
            case "subject": {
                if (!form.subject) {
                    setPost({
                        ...post,
                        [e.target.name]: false
                    })
                    return setForm({
                        ...form,
                        [e.target.name]: e.target.value,
                    })
                } else if (form.subject.length >= 40) {
                    SpanSubject.current.classList.remove('good');
                    SpanSubject.current.classList.add('error');
                    setPost({
                        ...post,
                        [e.target.name]: false
                    })
                    return setForm({
                        ...form,
                        [e.target.name]: form.subject.slice(0, -1),
                    })
                } else if (form.subject.length < 2) {
                    SpanSubject.current.classList.remove('good');
                    SpanSubject.current.classList.add('error');
                    setPost({
                        ...post,
                        [e.target.name]: false
                    })
                    return setForm({
                        ...form,
                        [e.target.name]: e.target.value,
                    })
                } else {
                    SpanSubject.current.classList.remove('error');
                    SpanSubject.current.classList.add('good');
                    setPost({
                        ...post,
                        [e.target.name]: true
                    })
                    return setForm({
                        ...form,
                        [e.target.name]: e.target.value,
                    })
                }
            }
            case "message": {
                if (!form.message) {
                    setPost({
                        ...post,
                        [e.target.name]: false
                    })
                    return setForm({
                        ...form,
                        [e.target.name]: e.target.value,
                    })
                } else if (form.message.length >= 255) {
                    SpanMessage.current.classList.remove('good');
                    SpanMessage.current.classList.add('error');
                    setPost({
                        ...post,
                        [e.target.name]: false
                    })
                    return setForm({
                        ...form,
                        [e.target.name]: form.message.slice(0, -1),
                    })
                } else if (form.message.length < 2) {
                    SpanMessage.current.classList.remove('good');
                    SpanMessage.current.classList.add('error');
                    setPost({
                        ...post,
                        [e.target.name]: false
                    })
                    return setForm({
                        ...form,
                        [e.target.name]: e.target.value,
                    })
                } else {
                    SpanMessage.current.classList.remove('error');
                    SpanMessage.current.classList.add('good');
                    setPost({
                        ...post,
                        [e.target.name]: true
                    })
                    return setForm({
                        ...form,
                        [e.target.name]: e.target.value,
                    })
                }
            }
            default:
                return form;
        }
    }, [form, post, regexEmail]);
    const handleSubmit = (e) => {
        e.preventDefault();
        let formPost = new FormData(e.target)
        console.log(post);
        if (post.name && post.email && post.subject && post.message) {
            let options = {
                method: "POST",
                body: formPost,
            };
            PostMail("https://formsubmit.co/ajax/leonardomr.develoop@gmail.com", options)
                .then(res => {
                    if (res.success === "true") {
                        setForm({});
                        setPost({});
                        setError(false);
                        setTimeout(() => {
                            setError(null);
                        }, 10000);
                    } else {
                        setError(true);
                        setTimeout(() => {
                            setError(null);
                        }, 10000);
                    }
                })
        }
    }

    const handleAlert = useCallback((e) => {
        if (theme) {
            switch (e.target.name) {
                case "name": {
                    return SpanName.current.classList.add('focus-light');
                }
                case "email": {
                    return SpanEmail.current.classList.add('focus-light');
                }
                case "subject": {
                    return SpanSubject.current.classList.add('focus-light');
                }
                case "message": {
                    return SpanMessage.current.classList.add('focus-light');
                }
                default:
                    return;
            }
        } else {
            switch (e.target.name) {
                case "name": {
                    return SpanName.current.classList.add('focus-dark');
                }
                case "email": {
                    return SpanEmail.current.classList.add('focus-dark');
                }
                case "subject": {
                    return SpanSubject.current.classList.add('focus-dark');
                }
                case "message": {
                    return SpanMessage.current.classList.add('focus-dark');
                }
                default:
                    return;
            }
        }
    }, [theme]);
    const handleRemoveAlert = useCallback((e) => {
        SpanName.current.classList.remove('focus-dark');
        SpanEmail.current.classList.remove('focus-dark');
        SpanSubject.current.classList.remove('focus-dark');
        SpanMessage.current.classList.remove('focus-dark');
        SpanName.current.classList.remove('focus-light');
        SpanEmail.current.classList.remove('focus-light');
        SpanSubject.current.classList.remove('focus-light');
        SpanMessage.current.classList.remove('focus-light');
    }, []);
    const handleIconShow = (e) => {
        if (e.target.alt === "LinkedIn") {
            Linkedin_ICON.current.classList.add("Contact_icon-active")
        } else if (e.target.alt === "GitHub") {
            GitHub_ICON.current.classList.add("Contact_icon-active")
        } else {
            WhatsApp_ICON.current.classList.add("Contact_icon-active")
        }
    };
    const handleIconHidden = (e) => {
        if (e.target.alt === "LinkedIn") {
            Linkedin_ICON.current.classList.remove("Contact_icon-active")
        } else if (e.target.alt === "GitHub") {
            GitHub_ICON.current.classList.remove("Contact_icon-active")
        } else {
            WhatsApp_ICON.current.classList.remove("Contact_icon-active")
        }
    };
    const array = [
        { icon1: whatsappW, icon2: whatsappB, ref: WhatsApp_ICON, title: "WhatsApp", url: "https://wa.me/584122177268" },
        { icon1: linkedinW, icon2: linkedinB, ref: Linkedin_ICON, title: "LinkedIn", url: "https://www.linkedin.com/in/leonardomonsalves" },
        { icon1: githubW, icon2: githubB, ref: GitHub_ICON, title: "GitHub", url: "https://github.com/JLeonardoMr" },
    ];
    return (
        <Row className={`${theme ? "Contact" : "Contact-dark"} py-3 justify-content-center`}>
            <Col className={`d-flex col-12`}>
                <Col className='col-12' style={{ position: "relative" }}>
                    <span
                        id='Contact'
                        className={"Contact_span d-flex justify-content-sm-start justify-content-center"}
                        style={{ color: `${theme ? "black" : "white"}`, fontSize: `${Width < 575 ? 18 : 15}vw` }}
                    >
                        {
                            lenguage
                                ? LenguageText.en.Contact.span
                                : LenguageText.es.Contact.span
                        }
                    </span>
                </Col>
            </Col>
            <Col sm={10} md={7} lg={5} className='col-12 d-flex justify-content-center'>
                <Col className='col-12 p-0 py-4 '>
                    <Form onSubmit={handleSubmit} className='row m-auto flex-sm-row flex-column justify-content-center'>
                        <FormGroup
                            sm={6}
                            controlId={'name'}
                            label={lenguage ? en.Contact.formName : es.Contact.formName}
                            type={"text"}
                            name={'name'}
                            placeholder={'...'}
                            defaultValue={form.name}
                            autoComplete={'off'}
                            handleForm={handleForm}
                            handleAlert={handleAlert}
                            handleRemoveAlert={handleRemoveAlert}
                            span={SpanName}
                        />
                        <FormGroup
                            sm={6}
                            controlId={'email'}
                            label={lenguage ? en.Contact.formEmail : es.Contact.formEmail}
                            type={"email"}
                            name={'email'}
                            placeholder={'@'}
                            defaultValue={form.email}
                            autoComplete={''}
                            handleForm={handleForm}
                            handleAlert={handleAlert}
                            handleRemoveAlert={handleRemoveAlert}
                            span={SpanEmail}
                        />
                        <FormGroup
                            sm={12}
                            controlId={'subject'}
                            label={lenguage ? en.Contact.formSubject : es.Contact.formSubject}
                            type={"text"}
                            name={'subject'}
                            placeholder={'...'}
                            defaultValue={form.subject}
                            autoComplete={'off'}
                            handleForm={handleForm}
                            handleAlert={handleAlert}
                            handleRemoveAlert={handleRemoveAlert}
                            span={SpanSubject}
                        />
                        <FormGroup
                            sm={12}
                            controlId={'message'}
                            label={lenguage ? en.Contact.formMessage : es.Contact.formMessage}
                            type={"textarea"}
                            name={'subject'}
                            placeholder={'...'}
                            defaultValue={form.message}
                            autoComplete={'off'}
                            handleForm={handleForm}
                            handleAlert={handleAlert}
                            handleRemoveAlert={handleRemoveAlert}
                            span={SpanMessage}
                        />
                        <Button
                            type='submit'
                            variant={theme ? "outline-dark" : "outline-light"}
                            className={`${theme ? "Contact_form-btn" : "Contact_form-btn-dark"} col-5`}
                        >
                            <b>{lenguage ? en.Contact.btn : es.Contact.btn}</b>
                        </Button>
                    </Form>
                    {
                        (error === true || error === false) && <Error variant={error ? "danger" : "success"} title={
                            error
                                ? lenguage ? en.Contact.error : es.Contact.error
                                : lenguage ? en.Contact.success : es.Contact.success
                        }
                        />
                    }

                </Col>
            </Col>
            <Col sm={10} md={7} lg={5} className='col-12 py-5 d-flex  justify-content-sm-around'>
                {
                    array.map(
                        (el, key) =>
                            <Col key={key} sm={2} className='col-4 d-flex align-items-center justify-content-sm-start  flex-column justify-content-center my-2 '>
                                <a
                                    href={el.url}
                                    target={'_blank'}
                                    rel="noreferrer"
                                >
                                    <Image
                                        onMouseEnter={handleIconShow}
                                        onMouseLeave={handleIconHidden}
                                        src={theme ? el.icon2 : el.icon1} alt={el.title}
                                        style={{ maxWidth: "50px", zIndex: 5 }}
                                    />
                                </a>
                                <div style={{ overflow: "hidden" }}>
                                    <h3
                                        style={{ color: `${theme ? "#272727" : "#B4A5A5"}` }}
                                        className={`Contact_icon ${Width < 576 ? "Contact_icon-active" : ""} px-2`}
                                        ref={el.ref}>{el.title}</h3>
                                </div>
                            </Col>
                    )
                }
            </Col>
        </Row>
    )
}
