import { Col, Row } from 'react-bootstrap';
import useLenguage from "../hooks/useLenguage";
import useTheme from '../hooks/useTheme';
import img_1 from '../asset/image/project1.jpg';
import img_2 from '../asset/image/project2.jpg';
import { IconsPortfolio } from '../components/icons';
import { useResize } from '../hooks/useResize';

export const Portfolio = () => {
    const { theme } = useTheme();
    const { lenguage, LenguageText } = useLenguage();
    const resize = useResize();
    const [Width] = resize !== 0 ? resize : [0, 0];
    const Portafolio = [
        {
            url: "/calculator",
            img: img_1,
            title: "Calculator"
        },
        {
            url: "/music_search",
            img: img_2,
            title: "MusicSearch"
        }
    ];
    return (
        <Row className={`${theme ? "Portfolio" : "Portfolio-dark"} py-3 flex-column`}>
            <Col className={`d-flex col-12`}>
                <Col className='col-12' style={{ position: "relative" }}>
                    <span
                        className={"Contact_span d-flex justify-content-sm-end justify-content-center"}
                        style={{ color: `${theme ? "black" : "white"}`, fontSize: `${Width < 575 ? 18 : 15}vw` }}
                    >
                        {
                            lenguage
                                ? LenguageText.en.Portfolio.h2
                                : LenguageText.es.Portfolio.h2
                        }
                    </span>
                </Col>
            </Col>
            <Col className='d-flex flex-column flex-sm-row py-4 px-0'>
                <Col className={"col-12 d-flex flex-wrap justify-content-around"} style={{ zIndex: 2, overflow: "hidden" }}>
                    {
                        Portafolio.map(
                            (el, key) => <IconsPortfolio key={key} url={el.url} img={el.img} title={el.title} />)
                            .filter((el, key) => key < 8 ? el : null)
                    }
                </Col>
            </Col>
        </Row>
    )
}

