import { useRef } from "react";
import { Button, Col, Row } from "react-bootstrap";
// import { Button, Col, Image, Row } from "react-bootstrap";
// import logo from "../asset/logomarca.svg"
// import logo2 from "../asset/logomarca2.svg"
import { AnimeStars } from "../components/animeStars";
import { GranimBackground } from "../components/granim";
import { ForestBackground } from "../components/forestBackground";
import useTheme from "../hooks/useTheme"
import useLenguage from "../hooks/useLenguage";
import { useResize } from "../hooks/useResize";
import { Link } from "react-router-dom";

export const Intro = () => {
    const resize = useResize();
    const [Width, Height] = resize !== 0 ? resize : [0, 0];
    const Span = useRef("span");
    const { theme } = useTheme();
    const { lenguage, LenguageText } = useLenguage();
    const { es, en } = LenguageText;
    const gradients = [
        ['#4FA8E4', '#EFFFFB'],
        ['#4FA8E4', '#EFFFFB'],
        ['#536A6F', '#EFFFFB'],
        ['#536A6F', '#EFFFFB'],
        ['#536A6F', '#EFFFFB'],
        ['#536A6F', '#EFFFFB'],
        ['#536A6F', '#EFFFFB'],
        ['#4FA8E4', '#EFFFFB'],
        ['#4FA8E4', '#EFFFFB'],
    ]

    return (
        <Row className="Intro" style={{ position: "relative", overflow: "hidden" }}>
            <Col className={theme ? "Intro_container" : "Intro_container-dark"} style={{ height: Height - 100, width: Width }}>
                <Col className="Intro_title col-12 pb-5 col-sm-6 justify-content-center justify-content-sm-end">
                    {
                        [...Array(4)].map((el, key) => <span key={key} ref={Span} style={{ animationDuration: `${(Math.random() * 50) + 50}s` }} />)
                    }
                    <Col sm={8} className={'pb-5'}>
                        <Col className="Intro_title-title">
                            <h1 style={{ color: `${theme ? "#272727" : "#B4A5A5"}` }}>{lenguage ? en.Intro.h1 : es.Intro.h1}
                                <br />
                                <b>{lenguage ? en.Intro.h1Two : es.Intro.h1Two}</b>
                            </h1>
                            <h2 style={{ color: `${theme ? "#272727" : "#B4A5A5"}` }}>{lenguage ? en.Intro.h2 : es.Intro.h2}</h2>
                            <h6 style={{ color: `${theme ? "#404040" : "#5F668D"}` }}>Front End / Web Developer / WordPress</h6>
                        </Col>
                        <Col className={`${theme ? "Intro_title-btn" : "Intro_title-btn-dark"} py-4 text-end text-xl-center`}>
                            <Link to="Contact">
                                <Button
                                    style={{ zIndex: 9999 }}
                                    variant={theme ? "outline-dark" : "outline-light"}
                                >
                                    <b>{lenguage ? en.Intro.btn : es.Intro.btn}</b>
                                </Button>
                            </Link>
                        </Col>
                    </Col>
                </Col>
                {Width >= 575
                    ? <Col className={theme ? "Intro_logo col-12 col-sm-6" : "Intro_logo-dark col-12 col-sm-6"}>
                        <div>
                            {
                                [...Array(4)].map((el, key) => <span key={key} />)
                            }
                            {/* <Image src={true ? logo2 : logo} alt="Intro_logo" /> */}
                        </div>
                    </Col>
                    : null
                }
            </Col>
            {!theme && <AnimeStars Width={Width} Height={Height} />}
            {theme && <Col className="Intro_stars" style={{ height: Height - 76, width: Width }}><GranimBackground gradient={gradients} /></Col>}
            <ForestBackground Width={Width} Height={Height} />
        </Row >
    );
};