import { Col, Image, Row } from 'react-bootstrap'
import useLenguage from "../hooks/useLenguage";
import useTheme from '../hooks/useTheme';
import light from '../asset/about-light.png'
import dark from '../asset/about-dark.png'


export const About = () => {
    const { theme } = useTheme();
    const { lenguage, LenguageText } = useLenguage();
    let array = lenguage ? LenguageText.en.About.h2.split("") : LenguageText.es.About.h2.split("");
    const handleMouseEnter = (e) => {
        e.target.classList.add("About_left-title_hover")
        setTimeout(() => {
            e.target.classList.remove("About_left-title_hover")
        }, 1000);
    }
    return (
        <Row className={`${theme ? "About" : "About-Dark"} py-5 justify-content-center`}>
            <Col sm={6} lg={5} className='col-6 d-flex justify-content-center'>
                <Image src={`${theme ? light : dark}`} alt={`${theme ? "About-light" : "Aboutdark"}`} style={{width:"100%", maxWidth:"290px", opacity:".7"}} />
            </Col>
            <Col sm={6} lg={5} className='col-12 d-flex flex-column'>
                <div className='d-flex justify-content-around w-50 mx-auto' style={{ height: "3.2rem" }}>
                    {array.map((el, key) => <h2 onMouseEnter={handleMouseEnter} key={key} className={theme ? 'About_left-title' : 'About_left-title-dark'}>{el}</h2>)}
                </div>
                <p className={`${theme ? "About_left-paragraph" : "About_left-paragraph-dark"} py-2`}>
                    {lenguage ? LenguageText.en.About.pOne : LenguageText.es.About.pOne}
                    {" "}
                    {lenguage ? LenguageText.en.About.pTwo : LenguageText.es.About.pTwo}
                </p>
                <p className={`${theme ? "About_left-paragraph" : "About_left-paragraph-dark"} py-2`}>
                    {lenguage ? LenguageText.en.About.pThree : LenguageText.es.About.pThree}
                </p>
                <p className={`${theme ? "About_left-paragraph" : "About_left-paragraph-dark"} py-2`}>
                    {lenguage ? LenguageText.en.About.pFor : LenguageText.es.About.pFor}
                </p>
            </Col>
        </Row>
    )
}
