import { useEffect } from "react";
import Granim from "granim/lib/Granim";

export const GranimBackground = ({ gradient }) => {
    useEffect(() => {
        new Granim({
            element: '#canvas-basic',
            direction: 'top-bottom',
            isPausedWhenNotInView: true,
            states: {
                "default-state": {
                    gradients: gradient
                }
            }
        });
    })
    return (
        <canvas id="canvas-basic" style={{ zIndex: "-11" }} ></canvas>
    )
}
