import { useLayoutEffect } from "react";
import { Container, Form, Image, Nav, Navbar, NavbarBrand } from "react-bootstrap";
import NavbarCollapse from "react-bootstrap/esm/NavbarCollapse";
import { NavLink, Outlet } from "react-router-dom";
import logo from "../asset/logomarca.svg"
import logo2 from "../asset/logomarca2.svg"
import { CheckedTheme } from "../components/checkedTheme";
import { OffCanva } from "../components/offcanva";
import { useActive } from "../hooks/useActive";
import useLenguage from "../hooks/useLenguage";
import { useResize } from "../hooks/useResize";
import useTheme from "../hooks/useTheme";
export default function Header() {
    const [isActive, On, Off] = useActive(false);
    const [isShow, Open, Close] = useActive(false);
    const resize = useResize();
    const [Width] = resize !== 0 ? resize : [0, 0];
    const { theme } = useTheme();
    const { handleChangeLenguage, lenguage, LenguageText } = useLenguage()
    useLayoutEffect(() => {
        document.documentElement.style.setProperty('--my-start-MinWidth', `${(-Width / 2) - 60}px`)
        document.documentElement.style.setProperty('--my-start-MaxWidth', `${(Width / 2) - 100}px`)
        document.documentElement.style.setProperty('--my-background-color', `${theme?"#fff":"#1d1127"}`)
    }, [Width, theme])
    const array = [
        {
            to: "/",
            h: "Home"
        }, {
            to: "about",
            h: lenguage ? LenguageText.en.About.h2 : LenguageText.es.About.h2
        }, {
            to: "skills",
            h: lenguage ? LenguageText.en.Skills.title : LenguageText.es.Skills.title
        }, {
            to: "contact",
            h: lenguage ? LenguageText.en.Contact.span : LenguageText.es.Contact.span
        },
    ];
    return (
        <>
            <Navbar className={`navBar ${theme ? "" : "navBar-dark"}`}>
                <Container fluid>
                    <Image src={theme ? logo : logo2} alt="logo" className="" />
                    {
                        Width >= 500 && <NavbarBrand style={{ color: `${theme ? "" : "white"}` }}>
                            JLeonardo Mr.
                        </NavbarBrand>
                    }
                    <NavbarCollapse className="justify-content-between">
                        <Nav >
                            <CheckedTheme />
                        </Nav>
                        <Nav>
                            {
                                Width >= 768 && array.map((el, key) =>
                                    <NavLink
                                        key={key}
                                        exact='true'
                                        style={{
                                            margin: "0 10px 0 10px",
                                            color: `${theme ? "#272727" : "#B4A5A5"}`,
                                            borderBottomColor: `${theme ? "#4F98CA" : "#3C415C"}`
                                        }}
                                        to={el.to}
                                        className={(navData) => navData.isActive
                                            ? "NavLinkActive"
                                            : "NavLink"}
                                    >
                                        <h6>{el.h}</h6>
                                    </NavLink>
                                )
                            }
                        </Nav>
                        <Nav >
                            <Form.Select size="sm" defaultValue={lenguage ? "En" : "Es"} onChange={handleChangeLenguage} style={{ color: `${theme ? "" : "white"}`, backgroundColor: `${theme ? "" : "#17121d"}` }}>
                                {lenguage ? <option>En</option> : <option>En</option>}
                                {lenguage ? <option>Es</option> : <option>Es</option>}
                            </Form.Select>
                        </Nav>
                    </NavbarCollapse>
                    {
                        Width <= 768 && <div onClick={() => { isActive ? Off() : On(); isShow ? Close() : Open() }}
                            className={`${theme ? "hamburger" : "hamburger-dark"} hamburger--emphatic ${isActive ? "is-active" : ""}`}
                            tabIndex="0"
                            aria-label="Menu"
                            role="button"
                            aria-controls="navigation"
                        >
                            <div className="hamburger-box">
                                <div className={`${theme ? "hamburger-inner" : "hamburger-inner-dark"}`}></div>
                            </div>
                        </div>
                    }
                </Container>
                <OffCanva show={isShow} close={Close} off={Off} />
            </Navbar>
            <Container fluid className="mt-6">
                <Outlet />
            </Container>
        </>
    );
};
