import { Col, Form } from "react-bootstrap"
import useTheme from '../hooks/useTheme';
export const FormGroup = ({
    sm,
    label,
    type,
    name,
    controlId,
    placeholder,
    defaultValue,
    autoComplete,
    handleForm,
    handleAlert,
    handleRemoveAlert,
    span
}) => {
    const { theme } = useTheme();
    if (type === "textarea") {
        return (
            <Form.Group as={Col} sm={12} className={'px-4 col-12'} controlId={controlId}>
                <Form.Label>{label}</Form.Label>
                <Form.Control
                    as={"textarea"}
                    rows={3}
                    name={'message'}
                    className={
                        theme
                            ? 'Contact_input'
                            : 'Contact_input-dark'
                    }
                    value={defaultValue ?? ""}
                    style={{ resize: "none", height: "10rem" }}
                    onChange={handleForm}
                    onFocus={handleAlert}
                    onBlur={handleRemoveAlert}
                />
                <div ref={span} className='Contact_span-alert'>
                    <span />
                </div>
            </Form.Group>
        )
    } else {
        return (
            <Form.Group as={Col} sm={sm} className={'px-4 col-12'} controlId={controlId}>
                <Form.Label>{label}</Form.Label>
                <Form.Control
                    required
                    name={name}
                    type={type}
                    placeholder={placeholder}
                    value={defaultValue ?? ""}
                    autoComplete={autoComplete}
                    className={
                        theme
                            ? 'Contact_input'
                            : 'Contact_input-dark'
                    }
                    onChange={handleForm}
                    onFocus={handleAlert}
                    onBlur={handleRemoveAlert}
                />
                <div ref={span} className='Contact_span-alert'>
                    <span />
                </div>
            </Form.Group>
        )
    }
}
