import { Col, Image } from "react-bootstrap";
import forest from "../asset/bg-forest.png";
import forest2 from "../asset/bg-forest-Recuperado.png";
import useTheme from "../hooks/useTheme";

export const ForestBackground = ({ Height, Width }) => {
    const { theme } = useTheme()
    return (
        <Col className="Intro_forest" style={{ height: Height - 76, width: Width, zIndex: "-9" }}>
            <Image
                src={theme?forest2:forest}
                alt="forest"
                style={{
                    marginTop: (Width > 1024) && (Width < 1160) ? Height - (Height / 2.75) : Height - 376,
                    marginLeft: Width > 1024
                        ? ""
                        : "-10%",
                    width: Width > 1024
                        ? Width
                        : ""
                }}
            />
        </Col>
    )
}
