import { useState } from "react";
import { Route, Routes } from "react-router-dom";
import { LENGUAGE as LenguageText, LenguageContext } from "./hooks/useLenguage";
import { ThemeContext } from "./hooks/useTheme";
import { About } from "./pages/About";
import { Calculator } from "./pages/Calculator";
import { Contact } from "./pages/Contact";
import Header from "./pages/Header";
import LandingPage from "./pages/LandingPage";
import MusicSearch from "./pages/MusicSearch";
import { Skills } from "./pages/Skills";

function App() {
  if (localStorage.length === 0) {
    localStorage.setItem("theme", false);
    localStorage.setItem("lenguage", "en");
  }
  let themeStorage, lenguageStorage;
  (localStorage.theme === "true") ? (themeStorage = true) : (themeStorage = false);
  (localStorage.lenguage === "en") ? (lenguageStorage = true) : (lenguageStorage = false);
  const [theme, setTheme] = useState(themeStorage);
  const [lenguage, setLenguage] = useState(lenguageStorage);
  // console.log("localstorage: ", localStorage, "theme: ", theme, "lenguage: ", lenguage);
  const handleChangeTheme = () => {
    if (theme) {
      localStorage.theme = false
      setTheme(false);
    } else {
      localStorage.theme = true
      setTheme(true);
    }
  }
  const handleChangeLenguage = (e) => {
    if (lenguage) {
      localStorage.lenguage = "es";
      setLenguage(false);
    } else {
      localStorage.lenguage = "en";
      setLenguage(true);
    }
  }
  return (
    <ThemeContext.Provider value={{ handleChangeTheme, theme }}>
      <LenguageContext.Provider value={{ handleChangeLenguage, lenguage, LenguageText }}>
        <Routes>
          <Route path={'/'} element={<Header />}>
            <Route index element={<LandingPage />} />
            <Route exact path="about" element={<About />} />
            <Route exact path="skills" element={<Skills />} />
            <Route exact path="contact" element={<Contact />} />
            <Route exact path="calculator" element={<Calculator />} />
            <Route exact path="music_search" element={<MusicSearch />} />
          </Route>
        </Routes>
      </LenguageContext.Provider>
    </ThemeContext.Provider>
  );
}

export default App;
