import React from 'react';
import { Offcanvas, OffcanvasTitle } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';
import useLenguage from '../hooks/useLenguage';
import useTheme from "../hooks/useTheme";

export const OffCanva = ({ show, close, off }) => {
    const { theme } = useTheme()
    const { lenguage, LenguageText } = useLenguage()
    const array = [
        {
            to: "/",
            h: "Home"
        }, {
            to: "about",
            h: lenguage ? LenguageText.en.About.h2 : LenguageText.es.About.h2
        }, {
            to: "skills",
            h: lenguage ? LenguageText.en.Skills.title : LenguageText.es.Skills.title
        }, {
            to: "contact",
            h: lenguage ? LenguageText.en.Contact.span : LenguageText.es.Contact.span
        },
    ];
    return (
        <Offcanvas
            show={show}
            onHide={() => { close(); off() }}
            style={{
                zIndex: 9999,
                color: `${theme ? "" : "white"}`,
                background: `${theme ? "" : "linear-gradient(0deg, rgba(34,23,50,1) 0%, rgba(23,18,29,1) 100%)"}`
            }}
        >
            {theme
                ? <Offcanvas.Header
                    closeButton
                    className='justify-content-arround'
                >
                    <OffcanvasTitle>
                        Menu
                    </OffcanvasTitle>
                </Offcanvas.Header>
                : <Offcanvas.Header
                    closeButton
                    closeVariant='white'
                    className='justify-content-arround'
                >
                    <OffcanvasTitle>
                        Menu
                    </OffcanvasTitle>
                </Offcanvas.Header>
            }


            <Offcanvas.Body>
                {
                    array.map((el, key) =>
                        <NavLink
                            key={key}
                            exact='true'
                            style={{
                                margin: "0 10px 0 10px",
                                color: `${theme ? "#272727" : "#B4A5A5"}`,
                                borderBottomColor: `${theme ? "#4F98CA" : "#3C415C"}`
                            }}
                            onClick={() => { close(); off() }}
                            to={el.to}
                            className={(navData) => navData.isActive
                                ? "NavLinkActive"
                                : "NavLink"}
                        >
                            <h6>{el.h}</h6>
                        </NavLink>
                    )
                }
            </Offcanvas.Body>
        </Offcanvas>
    )
};
