import React, { useCallback, useState } from 'react'
import { Button, Col, Image, Row } from 'react-bootstrap'
import useLenguage from "../hooks/useLenguage";
import useTheme from '../hooks/useTheme';
import html from "../asset/html.svg";
import css from "../asset/css.svg";
import js from "../asset/js.svg";
import react from "../asset/react.svg";
import redux from "../asset/redux.svg";
import git from "../asset/git.svg";
import sass from "../asset/sass.svg";
import bootstrap from '../asset/bootstrap.svg';
import material from '../asset/material-ui.svg';
import photoshop from '../asset/adobe-photoshop.svg';
import illustrator from '../asset/adobe-illustrator.svg';
import responsive from '../asset/responsive-design.png';
import coaching from '../asset/coaching.png';
import front_end from '../asset/front-end.png';
import adaptable from '../asset/adaptable.png';

import { Icons } from '../components/icons';
import { useResize } from '../hooks/useResize';
export const Skills = () => {
    const [frame, setFrame] = useState({})
    const { theme } = useTheme();
    const { lenguage, LenguageText } = useLenguage();
    const resize = useResize();
    const [Width] = resize !== 0 ? resize : [0, 0];
    const handleMouseEnter = useCallback((e, item) => {
        if (e.target.matches(".About_left-title") || e.target.matches(".About_left-title-dark")) {
            e.target.classList.add("About_left-title_hover")
            setTimeout(() => {
                e.target.classList.remove("About_left-title_hover")
            }, 1000);
        } else if (item.url && item.title) {
            setFrame({ url: item.url, alt: item.title })
        }
    }, [setFrame])
    const icons = [
        {
            title: "HTML",
            url: html
        },
        {
            title: "CSS3",
            url: css
        },
        {
            title: "JS",
            url: js
        },
        {
            title: "React.js",
            url: react
        },
        {
            title: "Redux",
            url: redux
        },
        {
            title: "Git",
            url: git
        },
        {
            title: "SASS",
            url: sass
        }, {
            title: "Bootstrap",
            url: bootstrap
        }, {
            title: "Material UI",
            url: material
        }, {
            title: "Photoshop",
            url: photoshop
        }, {
            title: "Illustrator",
            url: illustrator
        },
    ];
    const Skills_Icon = [
        {
            title: lenguage
                ? LenguageText.en.Skills.title1
                : LenguageText.es.Skills.title1,
            paragraph: lenguage
                ? LenguageText.en.Skills.p1
                : LenguageText.es.Skills.p2,
            url: responsive
        }, {
            title: lenguage
                ? LenguageText.en.Skills.title2
                : LenguageText.es.Skills.title2,
            paragraph: lenguage
                ? LenguageText.en.Skills.p2
                : LenguageText.es.Skills.p2,
            url: front_end
        }, {
            title: lenguage
                ? LenguageText.en.Skills.title3
                : LenguageText.es.Skills.title3,
            paragraph: lenguage
                ? LenguageText.en.Skills.p3
                : LenguageText.es.Skills.p3,
            url: coaching
        }, {
            title: lenguage
                ? LenguageText.en.Skills.title4
                : LenguageText.es.Skills.title4,
            paragraph: lenguage
                ? LenguageText.en.Skills.p4
                : LenguageText.es.Skills.p4,
            url: adaptable
        }

    ]
    return (
        <Row className={`${theme ? "Skills" : "Skills-dark"} py-3 justify-content-center`}>
            <Col sm={12} className='col-12 ' style={{ position: "relative" }}>
                <span
                    className={"Skill_span d-flex justify-content-sm-start justify-content-center"}
                    style={{ color: `${theme ? "black" : "white"}`, fontSize: `${Width < 575 ? 18 : 15}vw` }}
                >
                    {
                        lenguage
                            ? LenguageText.en.About.span
                            : LenguageText.es.About.span
                    }
                </span>
            </Col>
            <Col sm={6} lg={5} className='col-12'>
                <Col className='About_left-frame col-12 m-auto flex-column '>
                    {<>
                        <Image src={frame.url || icons[0].url} alt={frame.alt || icons[0].title} style={{ zIndex: 123 }} />
                        <h3>{frame.alt || icons[0].title}</h3>
                    </>}
                </Col>
                <Col className='About_left-frame'>
                    {
                        icons.map((el, key) => <Icons key={key} url={el.url} title={el.title} MouseEnter={handleMouseEnter} />)
                    }
                </Col>
            </Col>
            <Col sm={6} lg={5} className='col-12 d-flex row justify-content-center py-3'>
                {
                    Skills_Icon.map((el, key) =>
                        <Col key={key} sm={6} className={`${theme ? "About_right-frame" : "About_right-frame-dark"} col-11 d-flex my-1 `}>
                            <Button className='m-auto d-flex flex-column'>
                                <Image src={el.url} alt={el.title} style={{ zIndex: 123, maxWidth: "100%", minWidth: "65px" }} className='mx-auto my-1' />
                                <h6 className='mx-auto mb-2'>{el.title}</h6>
                                <p className='m-auto '>{el.paragraph}</p>
                            </Button>
                        </Col>
                    )
                }

            </Col>
        </Row>
    )
}
