import useTheme from "../hooks/useTheme"

export const CheckedTheme = () => {
    const { theme, handleChangeTheme } = useTheme()
    return (
        <div onClick={handleChangeTheme} className="checked_container">
            <div className={`checked_body ${theme ? "checkLight_body" : "checkDark_body"}`}>
                {
                    theme
                        ? [...Array(3)].map((el, key) => <span key={key} />)
                        : [...Array(5)].map((el, key) => <span key={key} />)
                }
                <div className={`checked_circle ${theme ? "checked_circle-light" : "checked_circle-dark"}`}>
                    {
                        theme
                            ? null
                            : [...Array(3)].map((el, key) => <span key={key} />)
                    }
                </div>
            </div>
        </div>
    )
}
